import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Home from './components/home'
import Layout from './hoc/layout'


export const Routes = () => {
    return (
        <Layout>
            <Switch>
                <Route path = "/" exact component = {Home} />
            </Switch>
        </Layout>
    )
}

export default Routes


